/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

*, *::before, *::after {
  box-sizing: border-box;
  font-family: 'Noto Sans', 'Roboto', sans-serif; }

.container {
  margin: 0 auto;
  max-width: 1100px; }
  @media (max-width: 768px) {
    .container {
      max-width: 960px; } }
  @media (max-width: 375px) {
    .container {
      max-width: 345px; } }

.nav {
  background: #AA0601;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 88px 29.43px 88px; }
  @media (max-width: 375px) {
    .nav img {
      padding: 15px 0; } }
  @media (max-width: 768px) {
    .nav {
      padding: 30px 39px 29.43px 39px; } }
  @media (max-width: 375px) {
    .nav {
      flex-direction: column;
      padding: 0 0; } }
  @media (max-width: 375px) {
    .nav-item {
      width: 50%; } }
  .nav-link {
    color: #ffffff;
    text-decoration: none;
    font-size: 20px;
    padding: 30px 32px;
    text-align: center; }
    @media (max-width: 768px) {
      .nav-link {
        padding: 0 20px; } }
    @media (max-width: 375px) {
      .nav-link {
        border: 1px solid #ffffff;
        padding: 8px 54px;
        display: block; } }
  .nav-list {
    display: flex; }
    @media (max-width: 375px) {
      .nav-list {
        flex-wrap: wrap;
        justify-content: center; } }

.header {
  height: 100%;
  background: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true") 100% 100%;
  background-size: cover;
  background-position: center;
  padding: 348px 0 352px 0;
  text-align: right;
  color: #650300; }
  .header h1 {
    font-size: 4rem; }
    @media (max-width: 768px) {
      .header h1 {
        font-size: 3rem; } }
    @media (max-width: 768px) {
      .header h1 {
        font-size: 2rem; } }
  .header h2 {
    font-size: 2.25rem; }
    @media (max-width: 768px) {
      .header h2 {
        font-size: 1rem; } }
